import React from 'react'

import { ContactContainer, FormGroup } from '../../styles/contactUsStyles'
import { Container, Button, DefaultSizes, VerticalPadding } from '../../styles/globalStyles'

// Context
// import {useGlobalStateContext, useGlobalDispatchContext} from '../../context/globalContext'

const Contact = () => {
    return(
        <ContactContainer>
            <Container>
                <VerticalPadding>
                    <DefaultSizes black>
                        <h1>Contacte-nos!</h1>
                        <p>Entre em contacto connosco e responderemos o mais rapidamente possível.</p>
                    </DefaultSizes>
                
                
                

                <form>
                    <FormGroup>
                        <label htmlFor="name">Nome</label>
                        <input type="text" id="name"/>
                    </FormGroup>
                    <FormGroup className="form-group">
                        <label htmlFor="email">Email</label>
                        <input type="text" id="email"/>
                    </FormGroup>
                    <FormGroup className="form-group">
                        <label htmlFor="message">Mensagem</label>
                        <textarea type="text" id="message"></textarea>
                    </FormGroup>
                    <Button>
                        <button id="new-email" class="anim-up">Enviar</button>
                    </Button>
                    
                    
                </form>

                </VerticalPadding>
            </Container>
            
            
        </ContactContainer>
    );
}

export default Contact;